import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";

import {
  Button,
  CardGrid,
  ContentCard,
  Group,
  Panel,
  PanelHeader,
} from "@vkontakte/vkui";
import { Icon28QrCodeOutline } from "@vkontakte/icons";
import bridge from "@vkontakte/vk-bridge";

const History = ({ id, setActivePanel, setCurentLink, data, setData }) => {
  const go = (e) => {
    bridge.send("VKWebAppOpenCodeReader").then(({ code_data }) => {
      if (code_data) {
        //window.location = code_data;
        const id = code_data
          .replace("https://card.myqrcards.com/links/", "")
          .replace(/\?v=[0-9]*/gm, "");
        setCurentLink(code_data);
        //setData(data.concat(id));
        //bridge.send("VKWebAppStorageSet", { key: id, value: code_data });
        setActivePanel("frame");
      }
    });
  };
  const open = (code_data) => () => {
    setCurentLink("https://card.myqrcards.com/links/" + code_data + "/info");
    setActivePanel("frame");
  };
  return (
    <Panel id={id}>
      <PanelHeader>История</PanelHeader>
      <Group>
        <CardGrid size="l">
          {data.map((item) => (
            <ContentCard
              key={(item && item.name) || item}
              subtitle={(item && item.company) || item}
              header={(item && item.name) || item}
              caption={(item && item.work) || item}
              image={(item && item.img) || null}
              onClick={open(item)}
            />
          ))}
        </CardGrid>
      </Group>

      <Button
        style={{ position: "fixed", right: 10, bottom: 10, zIndex: 2 }}
        size="m"
        onClick={go}
      >
        <Icon28QrCodeOutline />
      </Button>
    </Panel>
  );
};

History.propTypes = {
  id: PropTypes.string.isRequired,
  setActivePanel: PropTypes.func.isRequired,
  setCurentLink: PropTypes.func.isRequired,
};

export default History;
